*{box-sizing:border-box}html{font-family:'aulp', sans-serif}a{text-decoration:none;color:black}body{margin:0;padding:0;height:750px}.page-container{width:100%}.page-container_background-coffemilk{background-color:#f5b050}.page-container_height{height:61px}.header-container{width:100%;position:fixed;box-shadow:0 -2px 15px black;background-color:#fff}.content-container{max-width:1250px;margin:auto}.menu{display:flex;justify-content:space-between;align-items:center;transition:height 0.5s}.menu__link{display:inline-block;font-weight:bold;margin:0 15px;height:100%;padding:20px 10px;transition:color 0.3s}.menu__link:hover{color:#f5b050}.menu__container{list-style:none;margin:0;padding:0;transition:font-size 0.3s}.menu__element{display:inline-block}.menu-icon{height:21px;width:30px;flex-direction:column;justify-content:space-between;display:none;margin:20px 10px}.menu-icon_active .menu-icon__element:first-child{transform:translate(0, 10px) rotate(-45deg)}.menu-icon_active .menu-icon__element:nth-of-type(2){display:none}.menu-icon_active .menu-icon__element:last-child{transform:translate(0, -9px) rotate(45deg)}.menu-icon__element{border:1px solid black;width:100%;transition:all 1s}@media screen and (max-width: 767px){.menu{align-items:flex-start}.menu__container{font-size:0}.menu__container_active{background-color:#fff;font-size:16px;position:absolute;width:100%;top:60px;right:0}.menu__container_active .menu__element{display:block}.menu-icon{display:flex}}.section-container{max-width:1200px;padding:55px 5px;margin:auto}.section-container_flex{display:flex;justify-content:center;align-items:center;flex-wrap:wrap}.section-container_reverse{flex-direction:row-reverse}.section-container__img{width:650px}.content{width:500px;margin:20px}.content__title{font-size:32px}.content__title_white{color:white}.content__button{padding:15px 40px;background-color:black;border:none;border-radius:40px;color:white;font-size:20px}.content__img_right{float:right}.list_right{float:right}.list__element{margin:25px 0}
